import { useState } from 'react';
import { Box, Container, Typography, TextField, Button, Select, MenuItem, FormControl, InputLabel } from '@mui/material';
import { styled } from '@mui/material/styles';
import { BaseContainer, BaseTextField, BaseButton, BaseBox } from '../styles/globalStyles';
import { useNavigate } from 'react-router-dom';  // 
//  Alert 
import { Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Alert } from '@mui/material';
// Logo 
const Logo = () => (
  <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 4 }}>
    <svg viewBox="0 0 48 48" fill="none" width="16" height="16">
      <path
        d="M24 45.8096C19.6865 45.8096 15.4698 44.5305 11.8832 42.134C8.29667 39.7376 5.50128 36.3314 3.85056 32.3462C2.19985 28.361 1.76794 23.9758 2.60947 19.7452C3.451 15.5145 5.52816 11.6284 8.57829 8.5783C11.6284 5.52817 15.5145 3.45101 19.7452 2.60948C23.9758 1.76795 28.361 2.19986 32.3462 3.85057C36.3314 5.50129 39.7376 8.29668 42.134 11.8833C44.5305 15.4698 45.8096 19.6865 45.8096 24L24 24L24 45.8096Z"
        fill="currentColor"
      />
    </svg>
    <Typography variant="h6" sx={{ fontWeight: 'bold', color: 'white' }}>
      
    </Typography>
  </Box>
);
// ...  ...

const StyledDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialog-paper': {
    backgroundColor: '#1a1f25',
    color: '#fff',
    borderRadius: '1.5em',
    border: '.15em solid var(--primary)',
    backdropFilter: 'blur(4px)',
    boxShadow: 'inset 1px 1px 6px rgba(255,255,255,0.3), 2px 2px 15px rgba(0,0,0,0.5)',
    padding: theme.spacing(2)
  }
}));
// 
const StyledContainer = styled(Container)(({ theme }) => ({
  minHeight: '100vh',
  display: 'flex',
  flexDirection: 'column',
  backgroundColor: '#111418',
  color: '#fff',
  padding: theme.spacing(4)
}));

const StyledBox = styled(Box)(({ theme }) => ({
  width: '100%',
  maxWidth: '512px',
  margin: '0 auto',
  padding: theme.spacing(3),
}));
// Add this with other styled components
//  LoginBox 
const LoginBox = styled(BaseBox)(({ theme }) => ({
  maxWidth: '512px',
  margin: '0 auto',
  position: 'relative'  // 
}));
// 
const LoginContainer = styled(BaseContainer)(({ theme }) => ({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  padding: theme.spacing(4),
  position: 'relative',  // 
  minHeight: '100vh'     // 
}));

const LoginTextField = styled(BaseTextField)(({ theme }) => ({
  '& .MuiOutlinedInput-root': {
    height: '56px',
    backgroundColor: '#293038',
    borderRadius: '12px',
    color: '#fff',
    '& fieldset': {
      border: 'none',
    },
    '&:hover fieldset': {
      border: 'none',
    },
    '&.Mui-focused fieldset': {
      border: 'none',
    }
  }
}));

const LoginButton = styled(BaseButton)(({ theme }) => ({
  height: '48px',
  padding: '12px',
  '&.outlined': {
    backgroundColor: 'transparent',
    '&:hover': {
      backgroundColor: 'rgba(120, 255, 205, 0.1)'
    }
  }
}));
//  BackgroundCircles 
const BackgroundCircles = styled(Box)(({ theme }) => ({
  position: 'absolute',  //  absolute
  width: '100%',
  height: '100%',
  zIndex: 0,
  '& span': {
    position: 'absolute',
    borderRadius: '50%',
    opacity: 0,    //  filter: 'blur(2px)'
    '&:nth-of-type(1)': {  // 
      width: '180px',
      height: '180px',
      background: '#81D8D0',  // 
      left: '-80px',
      top: '50px',
      animation: 'am2 ease-out 1s forwards, float 3s ease-in-out infinite alternate'
    },
    '&:nth-of-type(2)': {  //
      width: '100px',
      height: '100px',
      background: '#E6C786',  // 
      right: '-10px',
      top: '80px',
      animation: 'am2 ease-out 1s 0.2s forwards, float 4s ease-in-out infinite alternate-reverse'
    },
    '&:nth-of-type(3)': {  // 
      width: '220px',
      height: '220px',
      background: '#97A5C0',  // 
      right: '-100px',
      bottom: '50px',
      animation: 'am2 ease-out 1s 0.4s forwards, float 3.5s ease-in-out infinite alternate'
    }
  },
  '@keyframes float': {
    '0%': { transform: 'translateY(0)' },
    '100%': { transform: 'translateY(-20px)' }
  },
  '@keyframes am2': {
    '0%': { 
      transform: 'scale(0) rotateZ(60deg)',
      opacity: 0
    },
    '100%': { 
      transform: 'scale(1) rotateZ(0)',
      opacity: 0.7
    }
  }
}));
// FormContainer  blur 
const BackgroundAnimation = styled(Box)({
  '.bg': {
    animation: 'am2 ease-out 1s'
  },
  '.glass': {
    animation: 'am3 ease-out 1s 0.2s backwards'
  },
  '@keyframes am1': {
    '0%': { transform: 'translateY(0)' },
    '100%': { transform: 'translateY(-30px)' }
  },
  '@keyframes am2': {
    '0%': { transform: 'scale(0, 0) rotateZ(60deg)' },
    '100%': { transform: 'scale(1, 1) rotateZ(0)' }
  },
  '@keyframes am3': {
    '0%': { 
      transform: 'translateY(8px)',
      opacity: 0
    },
    '100%': { 
      transform: 'translateY(0)',
      opacity: 1
    }
  }
});

// FormContainer 
const FormContainer = styled(Box)(({ theme }) => ({
  position: 'relative',
  padding: theme.spacing(5),
  border: '.15em solid var(--primary)',
  borderRadius: '1.5em',
  backgroundColor: 'rgba(255, 255, 255, 0.03)',
  backdropFilter: 'blur(4px)',
  boxShadow: 'inset 1px 1px 6px rgba(255, 255, 255, 0.3), 2px 2px 15px rgba(0, 0, 0, 0.5)',
  marginBottom: theme.spacing(3),
  zIndex: 1,
  animation: 'am3 ease-out 1s 0.2s backwards',
  '& form': {
    marginTop: theme.spacing(4)
  }
}));

//  Login 
// 
const TEST_USER = {
  ldapAccount: "410012409",
  password: "123456"
};

const Login = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    ldapAccount: '',
    password: '',
  });
  const [openDialog, setOpenDialog] = useState(false);
  const [showAlert, setShowAlert] = useState(false);

  const handleLogin = (isAccessible) => {
    // 
    if (formData.ldapAccount === TEST_USER.ldapAccount && 
        formData.password === TEST_USER.password) {
      if (isAccessible) {
        // 
        sessionStorage.setItem('isLoggedIn', 'true');
        sessionStorage.setItem('userAccount', formData.ldapAccount);
        navigate('/instruments');
      } else {
        // 
        const confirmLogin = window.confirm('');
        if (confirmLogin) {
          sessionStorage.setItem('isLoggedIn', 'true');
          sessionStorage.setItem('userAccount', formData.ldapAccount);
          navigate('/instruments');
        }
      }
    } else {
      alert('');
    }
  };

  const handleConfirm = () => {
    sessionStorage.setItem('isLoggedIn', 'true');
    sessionStorage.setItem('userAccount', formData.ldapAccount);
    setOpenDialog(false);
    navigate('/dashboard');
  };

  return (
    <BackgroundAnimation>
      <LoginContainer>
        <LoginBox>
          <BackgroundCircles className="bg">
            <span></span>
            <span></span>
            <span></span>
          </BackgroundCircles>
          
          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'flex-end', 
            mb: 2 
          }}>
            <Button 
              sx={{ 
                color: 'var(--primary)',
                fontSize: '0.9em'
              }}
            >
               | ENGLISH
            </Button>
          </Box>

          <FormContainer>
            <Typography variant="h4" sx={{ 
              fontSize: '24px',
              fontWeight: 'bold',
              mb: 3,
              textAlign: 'center'
            }}>
              
            </Typography>
            
            <Typography sx={{ 
              mb: 3,
              textAlign: 'center',
              color: 'rgba(255,255,255,0.7)'
            }}>
               LDAP 
            </Typography>

            <form>
              <Box sx={{ mb: 3 }}>
                <Typography sx={{ mb: 1 }}></Typography>
                <LoginTextField 
                  fullWidth 
                  placeholder=" LDAP "
                  value={formData.ldapAccount}
                  onChange={(e) => setFormData({ ...formData, ldapAccount: e.target.value })}
                />
              </Box>
              
              <Box sx={{ mb: 3 }}>
                <Typography sx={{ mb: 1 }}></Typography>
                <LoginTextField 
                  fullWidth 
                  type="password"
                  placeholder=""
                  value={formData.password}
                  onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                />
              </Box>
              <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
                <LoginButton 
                  fullWidth 
                  onClick={() => handleLogin(false)}
                >
                  
                  <span className="dot"></span>
                </LoginButton>
                <LoginButton 
                  fullWidth 
                  className="outlined"
                  onClick={() => handleLogin(true)}
                >
                  
                  <span className="dot"></span>
                </LoginButton>
              </Box>
                    {/*  */}
                  <StyledDialog
                    open={openDialog}
                    onClose={() => setOpenDialog(false)}
                  >
                    <DialogTitle sx={{ 
                      textAlign: 'center',
                      fontSize: '20px',
                      fontWeight: 'bold',
                      pb: 1
                    }}>
                      
                    </DialogTitle>
                    <DialogContent>
                      <DialogContentText sx={{ 
                        color: 'rgba(255,255,255,0.7)',
                        textAlign: 'center'
                      }}>
                        
                      </DialogContentText>
                    </DialogContent>
                    <DialogActions sx={{ 
                      justifyContent: 'center',
                      gap: 2,
                      pb: 3,
                      pt: 2
                    }}>
                      <LoginButton onClick={handleConfirm}>
                        
                      </LoginButton>
                      <LoginButton 
                        className="outlined"
                        onClick={() => setOpenDialog(false)}
                      >
                        
                      </LoginButton>
                    </DialogActions>
                  </StyledDialog>
              <Box sx={{ 
                display: 'flex', 
                justifyContent: 'center',
                gap: 2 
              }}>
                <Button 
                  color="primary" 
                  sx={{ 
                    textDecoration: 'underline',
                    color: 'var(--primary)'
                  }}
                >
                  
                </Button>
                <Button 
                  color="primary"
                  sx={{ 
                    textDecoration: 'underline',
                    color: 'var(--primary)'
                  }}
                >
                  
                </Button>
              </Box>
            </form>
          </FormContainer>
        </LoginBox>
      </LoginContainer>
    </BackgroundAnimation>
  );
};

export default Login;